import React from 'react';
import {
  DndContext,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
  DragEndEvent,
} from '@dnd-kit/core';
import {
  arrayMove,
  SortableContext,
  sortableKeyboardCoordinates,
  useSortable,
  horizontalListSortingStrategy,
} from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import { restrictToHorizontalAxis } from '@dnd-kit/modifiers';
import { StockItem } from './types'; // Assuming types.ts is in the same directory or adjust path
import {
  Box,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  IconButton,
  Card, 
  CardContent
} from '@mui/material';
import { Theme, useTheme } from '@mui/material/styles';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import FlagIcon from '@mui/icons-material/Flag';

// --- Helper Function (Moved from useStockItems) ---
const isItemLate = (item: StockItem): boolean => {
  if (item.currentStatus !== 'Pending Delivery') {
    return false;
  }
  // Add error handling for invalid date strings
  try {
      const orderDate = new Date(item.orderDate);
      // Check if date is valid before proceeding
      if (isNaN(orderDate.getTime())) {
          console.warn(`Invalid orderDate format for item ${item.id}:`, item.orderDate);
          return false;
      }
      const sevenDaysAgo = new Date();
      sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7);
      return orderDate < sevenDaysAgo;
  } catch (e) {
      console.error(`Error parsing orderDate for item ${item.id}:`, e);
      return false;
  }
};

// --- Reused Interfaces/Types (from ToolApp) ---
export interface ColumnConfig { // Moved here
  id: keyof StockItem | 'actions';
  label: string;
}

// --- Draggable Header Component (Moved) ---
interface DraggableHeaderProps {
  column: ColumnConfig;
  theme: Theme;
}

const DraggableHeader: React.FC<DraggableHeaderProps> = ({ column, theme }) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: column.id });

  const style: React.CSSProperties = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.5 : 1,
    touchAction: 'none',
  };

  const isNumeric = ['quantity', 'pricePerItem'].includes(column.id);

  return (
    <TableCell 
      ref={setNodeRef} 
      component="th"
      scope="col" 
      style={style} 
      {...attributes} 
      {...listeners}
      align={isNumeric ? 'right' : 'left'}
      sx={{ 
        fontWeight: 'bold', 
        cursor: 'grab', 
        whiteSpace: 'nowrap',
        bgcolor: isDragging ? (theme.palette.action.selected || '#e0e0e0') : theme.palette.background.paper,
        ...(column.id === 'isFlagged' && { width: 50, p: '6px 8px' }),
        ...(column.id === 'seller' && { minWidth: 140 }),
        ...(column.id === 'deliveryName' && { minWidth: 160 }),
        ...(column.id === 'quantity' && { width: 80 }),
        ...(column.id === 'destination' && { minWidth: 110 }),
        ...(column.id === 'pricePerItem' && { width: 100 }),
        ...(column.id === 'orderNumber' && { width: 110 }),
        ...(column.id === 'purchaseStatus' && { width: 110 }),
        ...(column.id === 'orderDate' && { width: 110 }),
        ...(column.id === 'asinSku' && { width: 120 }),
        ...(column.id === 'productName' && { minWidth: 160 }),
        ...(column.id === 'actions' && { width: 60, p: '6px 8px' }),
        ...(column.id === 'currentStatus' && { width: 100 }),
      }}
    >
      {column.label}
    </TableCell>
  );
};

// --- Mobile Item Card Component (Moved) ---
interface MobileItemCardProps {
  item: StockItem;
  onViewDetails: (itemId: string) => void;
  onActionClick: (event: React.MouseEvent<HTMLElement>, item: StockItem) => void;
  cardSx?: { backgroundColor?: string; hoverBackgroundColor?: string };
  theme: Theme;
}

const MobileItemCard: React.FC<MobileItemCardProps> = ({ item, onViewDetails, onActionClick, cardSx, theme }) => {
  return (
    <Card 
      variant="outlined"
      sx={{
        mb: 2, 
        cursor: 'pointer', 
        backgroundColor: cardSx?.backgroundColor || 'transparent',
        '&:hover': {
            backgroundColor: cardSx?.hoverBackgroundColor || (theme.palette.action.hover ?? 'rgba(0, 0, 0, 0.04)')
        }
      }} 
      onClick={() => onViewDetails(item.id)}
    > 
      <CardContent sx={{ pb: '8px !important' }}> 
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
          <Box sx={{ flexGrow: 1, mr: 1 }}>
            <Typography variant="subtitle1" component="div" sx={{ fontWeight: 'bold', lineHeight: 1.3 }}>
              {item.productName || item.deliveryName || 'Unnamed Item'}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              Status: {item.currentStatus}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              Qty: {item.quantity ?? 'N/A'} | Seller: {item.seller || 'N/A'}
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ fontSize: '0.8rem' }}>
              Order Date: {item.orderDate ? new Date(item.orderDate).toLocaleDateString() : 'N/A'}
            </Typography>
          </Box>
           <IconButton size="small" onClick={(e) => {
             e.stopPropagation(); 
             onActionClick(e, item);
           }} sx={{ mt: -1, mr: -1 }}> 
             <MoreVertIcon />
           </IconButton>
         </Box>
      </CardContent>
    </Card>
  );
};

// --- StockItemList Component ---
interface StockItemListProps {
  items: StockItem[];
  loading: boolean;
  isMobile: boolean;
  visibleColumns: ColumnConfig[];
  visibleColumnIds: Array<ColumnConfig['id']>;
  onColumnOrderChange: (newColumnIds: Array<ColumnConfig['id']>) => void;
  onViewDetails: (itemId: string) => void;
  onMenuOpen: (event: React.MouseEvent<HTMLElement>, item: StockItem) => void;
}

const StockItemList: React.FC<StockItemListProps> = ({
  items,
  loading,
  isMobile,
  visibleColumns,
  visibleColumnIds,
  onColumnOrderChange,
  onViewDetails,
  onMenuOpen
}) => {
  // --- DND Logic (Moved) ---
  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;
    if (over && active.id !== over.id) {
      const oldIndex = visibleColumnIds.indexOf(active.id as (keyof StockItem | 'actions'));
      const newIndex = visibleColumnIds.indexOf(over.id as (keyof StockItem | 'actions'));
      if (oldIndex === -1 || newIndex === -1) {
        console.error('Dragged item ID not found in visible columns', { activeId: active.id, overId: over.id });
        return;
      }
      const newOrder = arrayMove(visibleColumnIds, oldIndex, newIndex);
      onColumnOrderChange(newOrder); // Call prop to update state in parent
    }
  };
  // --- END DND Logic ---

  // *** Get theme at the top level of the component ***
  const theme = useTheme();

  // --- Helper function to safely access theme properties ---
  const getSafeColor = (themeObj: Theme, path: string, fallback: string) => {
      const keys = path.split('.');
      let current: any = themeObj;
      for (const key of keys) {
          if (current && typeof current === 'object' && key in current) {
              current = current[key];
          } else {
              return fallback;
          }
      }
      return typeof current === 'string' ? current : fallback;
  };

  // Function to determine row style
  // Pass theme as an argument
  const getRowStyle = (item: StockItem) => {
    // Use the theme passed from the component scope
    let backgroundColor = 'transparent';
    let hoverBackgroundColor = getSafeColor(theme, 'palette.action.hover', 'rgba(0, 0, 0, 0.04)');

    switch (item.currentStatus) {
      case 'Delivered':
        backgroundColor = getSafeColor(theme, 'palette.success.50', '#e8f5e9');
        hoverBackgroundColor = getSafeColor(theme, 'palette.success.100', '#c8e6c9');
        break;
      case 'Issue':
        backgroundColor = getSafeColor(theme, 'palette.error.50', '#ffebee');
        hoverBackgroundColor = getSafeColor(theme, 'palette.error.100', '#ffcdd2');
        break;
      case 'Archived':
        backgroundColor = getSafeColor(theme, 'palette.grey.100', '#f5f5f5');
        hoverBackgroundColor = getSafeColor(theme, 'palette.grey.200', '#eeeeee');
        break;
      case 'Pending Delivery':
        // Check if a Pending Delivery item is late
        if (isItemLate(item)) {
            backgroundColor = getSafeColor(theme, 'palette.warning.50', '#fffde7');
            hoverBackgroundColor = getSafeColor(theme, 'palette.warning.100', '#fff9c4');
        }
        // Otherwise, it keeps the default transparent background and hover
        break;
      default:
        // Keep defaults (transparent background, standard hover)
        break;
    }

    // Check for lateness *after* status styling
    if (isItemLate(item)) {
        backgroundColor = getSafeColor(theme, 'palette.warning.light', '#fff3e0');
        hoverBackgroundColor = getSafeColor(theme, 'palette.warning.main', '#ffcc80');
    }

    return { backgroundColor, hoverBackgroundColor };
  };

  // --- Render Logic ---
  if (loading) {
    return <Typography>Loading items...</Typography>; // Or a Skeleton loader
  }

  // Mobile View
  if (isMobile) {
    return (
      <Box sx={{ p: 1 }}> 
        {items.map((item) => {
          const { backgroundColor, hoverBackgroundColor } = getRowStyle(item);
          return (
            <MobileItemCard 
              key={item.id} 
              item={item} 
              onViewDetails={onViewDetails}
              onActionClick={onMenuOpen}
              cardSx={{ backgroundColor, hoverBackgroundColor }}
              theme={theme}
            />
          );
        })}
        {items.length === 0 && !loading && (
           <Typography align="center" sx={{ mt: 2, color: 'text.secondary' }}>
             No items match the current filter.
           </Typography>
         )}
      </Box>
    );
  }

  // Desktop View (Table)
  return (
    <DndContext
      sensors={sensors}
      collisionDetection={closestCenter}
      onDragEnd={handleDragEnd}
      modifiers={[restrictToHorizontalAxis]}
    >
      <TableContainer component={Paper}>
        <Table size="small">
          <TableHead>
            <TableRow>
              <SortableContext items={visibleColumnIds} strategy={horizontalListSortingStrategy}>
                {visibleColumns.map((col) => (
                  <DraggableHeader key={col.id} column={col} theme={theme} />
                ))}
              </SortableContext>
            </TableRow>
          </TableHead>
          <TableBody>
            {items.map((item) => {
              const { backgroundColor, hoverBackgroundColor } = getRowStyle(item);
              return (
                <TableRow
                  key={item.id}
                  hover
                  onClick={() => onViewDetails(item.id)}
                  sx={{ 
                    cursor: 'pointer',
                    backgroundColor: backgroundColor,
                    '&:hover': {
                      backgroundColor: hoverBackgroundColor,
                    },
                  }}
                >
                  {visibleColumns.map((column) => {
                    return (
                      <TableCell 
                        key={column.id}
                        align={['quantity', 'pricePerItem'].includes(column.id) ? 'right' : 'left'}
                        sx={{ 
                          whiteSpace: 'nowrap', 
                          overflow: 'hidden', 
                          textOverflow: 'ellipsis', 
                          maxWidth: ['productName', 'deliveryName', 'seller'].includes(column.id) ? 200 : undefined,
                          ...(column.id === 'isFlagged' && { p: '6px 8px', textAlign: 'center' }),
                          ...(column.id === 'actions' && { p: '0px 8px', textAlign: 'center' }),
                        }}
                      >
                        {column.id === 'actions' ? (
                          <IconButton size="small" onClick={(e) => { e.stopPropagation(); onMenuOpen(e, item); }}>
                            <MoreVertIcon fontSize="small" />
                          </IconButton>
                        ) : column.id === 'isFlagged' ? (
                            item.isFlagged ? <FlagIcon fontSize="inherit" color="error" /> : null
                        ) : column.id === 'orderDate' || column.id === 'dateDelivered' ? (
                           item[column.id] ? new Date(item[column.id] as string).toLocaleDateString() : ''
                        ) : (
                          item[column.id as keyof StockItem] ?? ''
                        )}
                      </TableCell>
                    );
                  })}
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </DndContext>
  );
};

export default StockItemList; 